/* Desktop First */
@mixin for-mobile-layout {
  @media (max-width: $mobile-breakpoint) {
    @content;
  }
}

@mixin for-tablet-layout {
  @media (max-width: $tablet-breakpoint) {
    @content;
  }
}

@mixin for-normal-layout {
  @media (max-width: $normal-breakpoint) {
    @content;
  }
}

@mixin for-wide-layout {
  @media (max-width: $wide-breakpoint) {
    @content;
  }
}

/* Media  Mixins */
@mixin aspect-ratio($width, $height) {
  position: relative;
  overflow: hidden;

  &:before {
    display: block;
    content: '';
    width: 100%;
    padding-top: ($height / $width) * 100%;
    pointer-events: none;
  }

  & > img,
  & > iframe {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
  }

  > img {
    object-fit: cover;
  }
}

// Mixin for removing hover effects on iphone screen
@mixin hover-supported {
  @media not all and (pointer: coarse) {
    &:hover {
      @content;
    }
  }
}

@mixin quotes {
  &::before {
    content: open-quote;
  }
  &::after {
    content: close-quote;
  }
}
@mixin main-layout {
  display: grid;
  grid-template-columns: auto $wide-breakpoint auto;
}

@mixin background {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin pos-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@mixin avatar {
  height: 200px;
  width: 200px;
  background-color: $clr1;
  border-radius: 50%;
}
