.info-grid {
  padding-bottom: rem(80px);
  .container {
    background-color: white;
    display: flex;
    width: 90%;
    min-height: rem(450px);
    &:nth-child(2n) {
      flex-direction: row-reverse;
    }
    & > * {
      width: 50%;
    }
    .img {
      @include background;
      &.one {
        background-image: url('../../img/cocktail-1.jpg');
      }
      &.two {
        background-image: url('../../img/cocktail-2.jpg');
      }
      &.three {
        background-image: url('../../img/cocktail-3.jpg');
      }
    }
    .txt {
      padding: $spacing-xl;
      h1 {
        font-size: rem(32px);
        font-family: cormorant-bold;
        margin-block-end: 1.33rem;
      }
      p {
        color: $clr-gray;
        line-height: 1.65rem;
        margin-bottom: 1rem;
      }
      .btn {
        padding: 14px 24px;
        border-radius: 24px;
        border: 1px solid $clr-black;
      }
    }
  }
}
